/* General Styles */
body {
    font-family: Arial, sans-serif;
    background-color: #282c34;
    color: #ffffff;
    margin: 0;
    padding: 0;
}

.app {
    padding: 20px;
    text-align: left; /* Align everything to the left by default */
}

/* Title Styling */
.title {
    margin-bottom: 20px;
    text-align: left; /* Align title to the left */
}

/* Input Section Styling */
.input-section {
    margin-top: 20px;
    display: flex; /* Align input groups horizontally */
    gap: 20px; /* Add spacing between input groups */
    padding: 20px; /* Padding inside the section for consistent spacing */
    border: 2px solid #50555e; /* Outline the grouping */
    border-radius: 8px; /* Rounded corners for consistency */
    background-color: transparent; /* Keep the original background */
    justify-content: space-between; /* Distribute input groups horizontally */
    align-items: flex-start; /* Align input groups at the top */
}

.input-group {
    flex: 1; /* Allow input groups to share available space equally */
    display: flex;
    flex-direction: column; /* Keep label and input stacked */
    align-items: flex-start; /* Align label and input to the left */
    min-width: 0; /* Prevent overflow issues */
}

.input-group label {
    margin-bottom: 5px;
    font-size: 16px;
    line-height: 1.5; /* Ensure consistent line spacing */
    height: 24px; /* Fix label height to align sections */
    display: flex; /* Align text properly even if wrapping */
    align-items: center; /* Center-align text vertically */
}

input {
    padding: 10px;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #50555e; /* Add a light border for better contrast */
    width: 100%; /* Ensure input fills available space */
    box-sizing: border-box; /* Include padding and border in width */
    text-align: left; /* Align text within the input to the left */
}

/* Button Group Styling */
.button-group {
    margin-top: 10px; /* Space between input field and buttons */
    display: flex;
    gap: 5px; /* Space between the buttons */
    width: 100%; /* Make the button group span the full width */
}

.button-group button {
    flex: 1; /* Make each button take an equal share of the width */
    padding: 10px; /* Ensure consistent button size */
    font-size: 16px;
    border: none;
    border-radius: 4px;
    background-color: #50555e;
    color: white;
    cursor: pointer;
    transition: background-color 0.2s ease;
    text-align: center;
}

.button-group button:hover {
    background-color: #61dafb;
    color: black;
}

/* Selection Section Styling */
.selection-section {
    display: flex;
    flex-direction: column; /* Stack sections vertically */
    align-items: flex-start; /* Align content to the left */
    gap: 20px; /* Space between sections */
    margin-bottom: 20px;
}

.selection-group {
    width: 100%; /* Ensure consistent alignment */
    text-align: left; /* Align text to the left */
}

.selection-group h3 {
    margin-bottom: 10px;
    text-align: left; /* Align heading to the left */
}

.level-buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    justify-content: flex-start; /* Align buttons to the left */
}

button {
    padding: 10px;
    width: 50px; /* Fixed width for consistency */
    height: 50px; /* Fixed height for consistency */
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    text-align: center;
    background-color: #3b3f46;
    color: white;
    transition: background-color 0.2s ease;
}

button:hover {
    background-color: #50555e;
}

button.selected {
    background-color: #61dafb;
    color: black;
}

/* Result Section Styling */
.result-section {
    margin-top: 20px;
    min-height: 180px; /* Reserve space for results */
    position: relative; /* Ensures static position in layout */
    border: 2px solid #50555e; /* Optional: visually distinguish results area */
    padding: 10px;
    border-radius: 10px;
    background-color: #3b3f46;
    display: flex;
    align-items: center; /* Vertically center the content */
    justify-content: center; /* Horizontally center the content */
    color: #ffffff;
    font-size: 16px;
    text-align: center; /* Center-align text within the results */
}

.result-section .result {
    text-align: center; /* Ensure the result text is centered */
}

.result-section .no-result,
.result-section .no-selection {
    color: #ff6f61;
    font-weight: bold;
}

.result-section .no-selection {
    color: #ffc107;
}

/* Error Message Styling */
.error-message p {
    color: #ff6f61;
    font-weight: bold;
    margin-top: 20px;
    text-align: left; /* Align error message to the left */
}

/* Extra Deck Count Styling */
.deck-count p {
    margin-top: 10px;
    font-size: 16px;
    text-align: left; /* Align deck count to the left */
}
